  margin-bottom: 5px;
}

.card-header p {
  margin: 0 0 10px 0;
}

.card-header img {
  width: 20px;
  height: 70%;
}

.card {
  background-color: #e8e8e8;
  width: 350px;
  min-height: 250px * 1.618;
  text-align: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: 10px 15px;
  
  box-shadow: -20px -20px 0px 0px lighten(#30475e, 5%);
  border-radius: 10px;
  
  -webkit-animation-name: shadow-show; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 1.5s; /* Safari 4.0 - 8.0 */
  animation-name: shadow-show;
  animation-duration: 1.5s;
  transition-timing-function: cubic-bezier(0.795, 0.000, 0.165, 1.000); /* custom */
}
  
.challenges-container h1,h2,h3,h4,h5 {
    margin: 0 0 10px 0;
    padding: 0px;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 1.4rem;
    color: #222831;
  }
  
  .challenges-container hr {
    display: block;
    border: none;
    height: 1.5px;
    background-color: #222831;
    margin: 0px;
    
    -webkit-animation-name: line-show; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 0.3s; /* Safari 4.0 - 8.0 */
    animation-name: line-show;
    animation-duration: 0.3s;
    transition-timing-function: cubic-bezier(0.795, 0.000, 0.165, 1.000); /* custom */
  }
  
  .challenges-container p {
    margin: 15px 0px 5px 0px;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 100;
    letter-spacing: -0.25px;
    line-height: 1.25;
    font-size: 16px;
    word-break: break-all;
    word-wrap: pre-wrap;
    color: #222831;
    
    -webkit-animation-name: p-show; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1.5s; /* Safari 4.0 - 8.0 */
    animation-name: p-show;
    animation-duration: 1.5s;
  }
  
  button {
    border: none;
    background-color: #f05454;
    width: 40%;
    margin-top: 10px;;
    padding: 10px 15px;
    color: #e8e8e8;
    font-family: 'Noto Sans KR', sans-serif;
    text-transform: uppercase;
  }

  .challenges-container a {
    margin: 0;
  }

/* Safari 4.0 - 8.0 */
@-webkit-keyframes line-show {
  from {margin: 0px 100px;}
  to {margin: 0px;}
}

/* Standard syntax */
@keyframes line-show {
  from {margin: 0px 100px;}
  to {margin: 0px;}
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes p-show {
  from {color: #e8e8e8;}
  to {color: #222831;}
}

/* Standard syntax */
@keyframes p-show {
  from {color: #e8e8e8;}
  to {color: #222831;}
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes shadow-show {
  from {
    box-shadow: 0px 0px 0px 0px #222831;
  }
  to {
    box-shadow: -20px -20px 0px 0px lighten(#e8e8e8, 5%);
  }
}

/* Standard syntax */
@keyframes shadow-show {
  from {box-shadow: 0px 0px 0px 0px #e0e0e0;}
  to { box-shadow: -20px -20px 0px 0px lighten(#696D7D, 5%);}
}

